import logo from "./logo.svg";
import "./App.css";
import Web3Modal from "web3modal";
import { ethers } from "ethers";
import { useState } from "react";
import WalletConnect from "@walletconnect/web3-provider";
import CoinbaseWalletSDK from "@coinbase/wallet-sdk";

function App() {
    console.log(process.env.INFURA_ID)
    const providerOptions = {
        walletconnect: {
            package: WalletConnect, // required
            options: {
                infuraId: "395c14b7c1d946ad85f472f3fcb106d3"// required
            }
        },
    };

    const web3Modal = new Web3Modal({
        network: "mainnet",
        theme: "dark", // optional, 'dark' / 'light',
        cacheProvider: false, // optional
        providerOptions, // required
    });

    const [connectedAccount, setConnectedAccount] = useState("");
    const [modalShown, toggleModal] = useState(false);

    const connectWeb3Wallet = async () => {
        try {
            const web3Provider = await web3Modal.connect();
            const library = new ethers.providers.Web3Provider(web3Provider);
            const web3Accounts = await library.listAccounts();
            setConnectedAccount(web3Accounts[0]);
            console.log(connectedAccount)
        } catch (error) {
            console.log(error);
        }
    };

    const disconnectWeb3Modal = async () => {
        await web3Modal.clearCachedProvider();
        setConnectedAccount("");
    };

    return (
        <div className="App" >



            <div className="app__content">
                <div className="app__logo">
                    <img src="https://matsurialpha.com/img/logo.png"/>
                </div>
                <div className="app__main">
                    <img className="app__gif" draggable="false" src="https://bafybeifa2izyyjgvfwegtjlvcsitbtwhr7q44dhrvmeziwoakeye6yfiiq.ipfs.nftstorage.link/"/>
                    <div className="app__nft_data">
                        <div className="app__text">
                            <div className="app__text__header">
                                If you mint 3+ NFT, you will be airdropped one of 333 Matsuri Girls NFT
                            </div>
                            <p className="app__text__main">
                                <span>Holders of Matsuri Girls receive:</span>
                                <span>- 1% royalties from Matsuri NFT sales</span>
                                <span>- 10% all royalties from sales of Matsuri Girls</span>
                            </p>
                            <div>By holding this NFT you become partner of the project and receive passive income. And other interesting benefits in the future.</div>


                        </div>
                        <div className="app__footer">
                            <div className="app__footer__header">
                                <div className="app__footer__header_title">TOTAL MINTED</div>
                                <div className="app__footer__header_percent">0.00%</div>
                                <div className="app__footer__header_counter">0/1555</div>
                            </div>
                            <button className="app__footer__mint_button" onClick={() => toggleModal(true)}>MINT IS LIVE</button>


                        </div>
                    </div>


                </div>
                <div className="app__socials">
                    <a className="app__socials__item" target="_blank" href="https://discord.com/invite/zDHuzsXXyE">
                        <img draggable="false" src="https://matsurialpha.com/img/discord.svg" alt="discord"/>
                        discord
                    </a>
                    <a className="app__socials__item" target="_blank" href="https://twitter.com/matsuri_alpha">
                        <img draggable="false" src="https://matsurialpha.com/img/tw.svg" alt="discord"/>
                        twitter
                    </a>
                </div>
            </div>
            {modalShown ?
                <div>
                    <div className="app__modal__close" onClick={() => toggleModal(false)}>
                    </div>
                    <div className="app__modal">
                        <div className="app__modal__mint__main">
                            {!connectedAccount ? (
                                    <button className="btn btn-primary connect" onClick={connectWeb3Wallet} >Connect wallet</button>
                                ) :
                                <button className="btn btn-primary connect" onClick={disconnectWeb3Modal} >Disconnect wallet</button>
                            }
                            <div className="app__submodal__item__main">
                                <img className="app__modal__image" src="https://mint.matsurialpha.com/assets/nft-collection.jpg"/>
                                <h2 className="app_modal__h__text">MATSURI MINT EVENT</h2>
                                <p className="app_modal__p__text">MATSURI COLLECTION</p>
                                <div className="app__submodal__mint__main">
                                    <button className="btn btn-primary connect app__modal__mint__config">Mint</button>
                                    <button className="btn btn-primary connect app__modal__mint__config">Mint</button>
                                </div>
                            </div>


                        </div>

                    </div>


                </div>: null}
        </div>
    );
}

export default App;